.dropzone {
    width: 100%;
    min-height: 200px;
    border: 2px dashed #666;
    border-radius: 5px;

    text-align: center;
    vertical-align: middle;
    color: #666;
}

.dropzone.active {
    border-style: solid;
    background-color: #eee;
}

.dropzone.accepted {
    border-style: solid;
    border-color: #6c6;
    background-color: #eee;
}

.dropzone.rejected {
    border-style: solid;
    border-color: #c66;
    background-color: #eee;
}

.dropzone.disabled {
    opacity: 0.5;
}

.fileList {
    overflow: auto;
    max-height: 200px;
}

.account {
    text-align: center;
}
.account .accountHistory {
    overflow: auto;
    max-height: 500px;
}
.account table {
    width: 100%;
    border-collapse: collapse;
}
.account thead th {
    min-width: 50px;
    border-bottom: 1px solid black;
}
.account tbody td, .account tfoot td {
    text-align: right;
    border-bottom: 1px solid transparent;
    padding: 1px 5px;
    cursor: default;
}
.account tbody tr.primary td.credit, .account tbody tr.primary td.debit {
    font-weight: bold;
}

.account .debit {
    border-right: 1px solid black;
}
.account .credit {
    color: #aa0000;
}
.account tfoot td {
    border-top: 3px solid black;
}
.account .comment {
    color: #666666;
    text-align: left;
}

.lineNumberValue {
    display: inline-block;
    min-width: 30px;
    text-align: right;
}

.account tr.hover td, .account tr:hover td {
    background: #dddddd;
}
.account tr.selected td {
    background: #6666aa;
}
.account tr.selected.hover td, .account tr.selected:hover td {
    background: #4444aa;
}
.account tr.selected td.comment {
    color: #dddddd;
}



